import {NgModule} from '@angular/core';
import {CommonModule} from '@angular/common';
import {IndexComponent} from './components/index/index.component';
import {DxTreeListModule} from 'devextreme-angular';
import {TemplateService} from '@modules/template/shared';
//library
import {devextremeModule} from '@core/index';

//general
import {routing} from './users.routing';
import {SharedModule} from '@shared/index';
import {UsersService} from './shared';
import {AddComponent} from './components/add/add.component';
import {vTranslateModule} from '@core/module/v-translate.module';
import {NotifyService} from '@modules/release-notify/shared';
import {ListsService} from '@modules/lists/shared';
import { NotifyEfyComponent } from '@shared/index';
import { RegisterService } from '@modules/auth/shared';
import { PermissionComponent } from './components/permission/permission.component';

@NgModule({
  imports: [
    CommonModule,
    routing,
    devextremeModule,
    DxTreeListModule,
    SharedModule,
    vTranslateModule
  ],
  declarations: [
    IndexComponent,
    AddComponent,
    PermissionComponent
  ],
  entryComponents: [
    AddComponent,
    NotifyEfyComponent,
    PermissionComponent
  ],
  providers: [
    UsersService,
    TemplateService,
    NotifyService,
    ListsService,
    RegisterService
  ]
})
export class UsersModule {
}
