import { Component, OnInit, ViewChild, ChangeDetectorRef } from '@angular/core';
import { BsModalService } from 'ngx-bootstrap/modal';
import { BsModalRef } from 'ngx-bootstrap/modal/bs-modal-ref.service';
import { User, UsersService } from '@modules/users/shared';
import { DxFormComponent, DxTreeListComponent, DxTreeViewComponent } from 'devextreme-angular';
import { Library, AuthService, VSelectBoxService, VStringService } from '@core/index';
import { TranslateService } from '@ngx-translate/core';
import { TemplateService } from '@modules/template/shared';
import { NotifyService } from '@modules/release-notify/shared';
import { DropdownCheckboxComponent } from '@shared/components/dropdown-checkbox/dropdown-checkbox.component';
import { RegisterService } from '@modules/auth/shared';
import { ConfigsService } from '@modules/configs/shared';
import { NumberUtilsService } from '@core/library/public/number-utils.service';

@Component({
  selector: 'app-add',
  templateUrl: './add.component.html',
  styleUrls: ['./add.component.scss']
})
export class AddComponent implements OnInit {
  @ViewChild(DxFormComponent) form: DxFormComponent;
  // @ViewChild(DxTreeListComponent) treeList: DxTreeListComponent;
  @ViewChild('listpermis') treeList: DxTreeListComponent;
  @ViewChild('listTempt') treeListTempt: DxTreeListComponent;
  @ViewChild('dropdownCheckboxComponent') dropdownCheckboxComponent: DropdownCheckboxComponent;
  @ViewChild('permissionbylist') treeView: DxTreeViewComponent;
  @ViewChild('listEmail') treeListEmail: DxTreeListComponent;
  // form validate
  user = new User();
  id: any;
  password = '';
  passwordOptions: any = {
    mode: 'password',
    value: this.password
  };
  namePattern: any = /^[^0-9]+$/;
  passwordPattern: any = /^.{6,}$/;
  emailPattern: any = /^([a-zA-Z0-9_\-\.]+)@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.)|(([a-zA-Z0-9\-]+\.)+))([a-zA-Z]{2,4}|[0-9]{1,3})(\]?)$/;
  userNamePattern: any = /^[a-zA-Z0-9_]{6,50}$/;
  phonePattern: any = /^(\(?\+?[0-9]*\)?)?[0-9_\- \(\)]*$/;
  isValidate = true;
  order: number;
  totalCount: any;
  disUserName = false;
  optionsPermissions: any;
  optionsEmails: any;
  remoteSigns: any;
  // treelist
  selectedRowKeys: any[] = [];
  selectedRowKeysTempt: any[] = [];
  selectedRowKeysEmail: any[] = [];
  recursiveSelectionEnabled = true;
  recursiveSelectionEnabledTempt = true;
  recursiveSelectionEnabledEmail = true;
  roles: string;
  permissions: string;
  successSaveItem: any;
  disableTemptlate = true;
  disableConfigEmail = true;
  remote_signing_info_default = "{\"is_use_remotesigning\":false ,\"agreement_uuid\":\"\",\"auth_mode\":\"\",\"authorize_code\":\"\"}";
  // quyn
  listpermissions: any;
  roleGroups = {
    KE_TOAN_VIEN: false,
    KE_TOAN_TRUONG: false,
    PHE_DUYET: false,
    QUAN_TRI_HE_THONG: false
  };
  checkKTV = false;
  checkKTT = false;
  checkPD = false;
  checkQTHT = false;
  _translate: any;
  isConfigAction = true;
  isConfigSignatues = false;
  isConfigTemplate = false;
  isConfigEmail = false;
  listTemplate: any;
  listTemplateTBThue: any[] = [];
  tabs = [];
  priority: any;
  isReceiveAllEmail: any;
  role_branch = false;
  roleBranchStatus: Array<any>;
  textTabs: string;
  lbl: any = {};
  isConnectEfyId = false;
  isUserConverted = false;
  currentUser: any;
  disableEmail = false;
  permissionByListType: any;
  valuePermission: any;
  dataListPermission: any[] = [];
  configs: any;
  listPermissionType: any[] = [];
  showTextValidPass: any;
  isDisRemoteSigning = true;
  authMode = [];
  passwordComparison = () => {
    return this.form.instance.option('formData').password;
  };
  listEmailConfigs: any[] = [];

  constructor(
    private authService: AuthService,
    public bsModalRef: BsModalRef,
    private usersService: UsersService,
    private modalService: BsModalService,
    private ref: ChangeDetectorRef,
    public translate: TranslateService,
    private templateService: TemplateService,
    private notifyService: NotifyService,
    private registerService: RegisterService,
    private configsService: ConfigsService,
  ) {
    this._translate = this.authService.getTranslate();
    translate.use(this.authService.getLanguage());
    this.tabs = VSelectBoxService._tabPermissionUser(this._translate);
    this.textTabs = this.tabs[0].code;
    this.optionsPermissions = [];
    this.optionsEmails = [
      { name: 'Nhn th t tt c cc email', code: '1' },
      { name: 'Nhn th t email', code: '0' }
    ];
    this.authMode = [
      {
        'code': 'EXPLICIT/PIN',
        'name': 'EXPLICIT/PIN',
      },
      {
        'code': 'EXPLICIT/OTP-SMS',
        'name': 'EXPLICIT/OTP-SMS',
      },
      {
        'code': 'EXPLICIT/OTP-EMAIL',
        'name': 'EXPLICIT/OTP-EMAIL',
      },
    ];
  }

  ngOnInit() {
    // this.getPermissions();
    this.currentUser = this.authService.getCurrentUser();

    this.configs = this.authService.getConfigs();
    if (this.currentUser && this.currentUser.account) {
      if (this.currentUser.account.connect_efy_id == "1")
        this.isConnectEfyId = true;
    }
    this.getUser();
    if (this.id) {
      this.isValidate = false;
      this.disUserName = true;
      if (this.configs.system_parameter.enable_modify_username)
        this.disUserName = false;
    }
    this.showTextValidPass = this._translate.VALIDATION.than6;
    if (this.configs.system_parameter.is_regex_pass) {
      this.passwordPattern = /^(?=.*[!@#$%^&*-])(?=(.*[0-9]){2,})(?=.*[A-Z])(?=.*[a-z]).{8,20}$/;
      this.showTextValidPass = this._translate.CONTROLL.LABEL.regex_password;
    }

    this.lbl = {
      label1: this._translate.CONTROLL.LABEL.user_will_use_efy_id_login + ' ' + this._translate.CONTROLL.LABEL.learn_more_about_efy_id,
      label2: this._translate.CONTROLL.LABEL.system_auto_email,
    };

  }
  getUser() {
    if (this.id) { // nu l edit
      this.usersService.getPermissions().subscribe(response => {
        this.listpermissions = response.data.permissions;
        if (this.configs.system_parameter.display_delivery_information) {
          this.permissionByListType.forEach(item => {
            if (item.note) {
              let lstRole = item.note.split(',');
              this.listpermissions.forEach(permission => {
                if (lstRole.includes(permission.code) && !permission.roles.includes(item.code))
                  permission.roles.push(item.code);
              });
            }
            let obj = {
              id: item.id,
              code: item.code,
              name: item.name,
              selected: false
            }
            this.listPermissionType.push(obj);
          });
        }
        // this.getUser();
        this.usersService.getUser(this.id).subscribe(response1 => {
          this.user = response1.data.user;
          this.priority = this.user.is_use_all_template ? '1' : '0';
          this.disableTemptlate = this.user.is_use_all_template;
          this.disableRadioTemplates();
          this.disableRadioEmail();
          this.user.status = NumberUtilsService._attemptConvertBool(this.user.status);

          if (this.user.roles) {
            if (this.configs.system_parameter.display_delivery_information) {
              const roles = this.user.roles.split(',');
              this.valuePermission = roles;
              this.listPermissionType.forEach(item => {
                if (roles.includes(item.code))
                  item.selected = true;
              });
            }
            else {
              const roles = this.user.roles.split(',');
              this.checkKTV = roles.includes('KE_TOAN_VIEN');
              this.checkKTT = roles.includes('KE_TOAN_TRUONG');
              this.checkPD = roles.includes('PHE_DUYET');
              this.checkQTHT = roles.includes('QUAN_TRI_HE_THONG');
            }
          }
          if (this.isConnectEfyId)
            this.disableEmail = true;
          if (this.user.permissions) {
            const permissions = this.user.permissions.split(',');
            this.listpermissions.forEach(item => {
              if (permissions.includes(item.code) && !this.treeList.instance.isRowSelected(item.id)) {
                this.selectedRowKeys.push(item.id);
              }
            });
          }
          if (this.user.remote_signing_info) {
            this.remoteSigns = JSON.parse(this.user.remote_signing_info);
            this.isDisRemoteSigning = !this.remoteSigns.is_use_remotesigning;
          }
          else
            this.remoteSigns = JSON.parse(this.remote_signing_info_default);
          this.templateService.getAll1().subscribe(rs => {
            this.listTemplate = rs.data.templates;
            if (this.listTemplate && this.listTemplate.length > 0) {
              this.listTemplateTBThue = [];
              let listTemptTT78 = this.listTemplate.filter(p => p.is_decree_new || p.template_code == "CTT56");
              if (listTemptTT78 && listTemptTT78.length > 0)
                this.listTemplateTBThue = listTemptTT78;
              this.notifyService.getAll1().subscribe(result => {
                const data = result.data;
                const listRelease = result.data.invoice_releases;
                if (listRelease && listRelease.length > 0) {
                  const listReleaseTBThue = listRelease.filter(p => p.release_status === 'DA_TB_THUE' || p.release_status === 'CHUA_TB_THUE' || p.release_status === 'DA_HUY_SO');
                  if (listReleaseTBThue && listReleaseTBThue.length > 0) {
                    listReleaseTBThue.forEach(release => {
                      this.listTemplate.forEach(template => {
                        if (template.id === release.template_id) {
                          const _obj = this.listTemplateTBThue.filter(p => p.id === template.id)[0];
                          if (!_obj) {
                            this.listTemplateTBThue.push(template);
                          }
                        }
                      });
                    });
                  }
                }
                this.listTemplateTBThue.forEach(item => {
                  item.name_display = item.template_code + ' - ' + item.invoice_series;
                });
                if (this.user.template_ids) {
                  const templates = this.user.template_ids.split(',');
                  if (this.listTemplateTBThue && this.listTemplateTBThue.length > 0) {
                    this.listTemplateTBThue.forEach(item => {
                      if (templates.includes(item.id)) {
                        this.selectedRowKeysTempt.push(item.id);
                      }
                    });
                  }
                }
              });
            }
          }, err => {
          }, () => {
            Library.hideloading();
          });
          /* Ly danh sch email ha n u vo */
          this.configsService.getAllEmails().subscribe(rs => {
            if (rs.status == "success") {
              this.listEmailConfigs = rs.data.config_mails;
              if (VStringService._getValueToString(this.user.permission_mails) != "") {
                this.disableConfigEmail = false;
                this.isReceiveAllEmail = '0';
                let emails = this.user.permission_mails.split(',');
                if (this.listEmailConfigs && this.listEmailConfigs.length > 0) {
                  this.listEmailConfigs.forEach(item => {
                    if (emails.includes(item.email)) {
                      this.selectedRowKeysEmail.push(item.id);
                    }
                  });
                }
              }
              else {
                this.isReceiveAllEmail = '1';
                this.disableConfigEmail = true;
              }
            }
          }, err => {
          }, () => {
            Library.hideloading();
          });
          if (this.role_branch) {
            if (this.user.role_branch) {
              const role_branch = this.user.role_branch.split(';');
              if (role_branch.indexOf('TAT_CA') !== -1) {
                this.roleBranchStatus.forEach((item) => {
                  item.checked = true;
                });
              } else {
                this.roleBranchStatus.forEach(item => {
                  if (role_branch.indexOf(item.code) !== -1) {
                    item.checked = true;
                  }
                });
              }
              this.dropdownCheckboxComponent.ngOnInit();
            }
          }
        });
      });
    } else {
      this.remoteSigns = JSON.parse(this.remote_signing_info_default);
      this.usersService.getPermissions().subscribe(response => {
        this.listpermissions = response.data.permissions;
        if (this.configs.system_parameter.display_delivery_information) {
          this.permissionByListType.forEach(item => {
            let lstRole = item.note.split(',');
            this.listpermissions.forEach(permission => {
              if (lstRole.includes(permission.code) && !permission.roles.includes(item.code))
                permission.roles.push(item.code);
            });
            let obj = {
              id: item.id,
              code: item.code,
              name: item.name,
              selected: false
            }
            this.listPermissionType.push(obj);
          });
        }
      });
      this.templateService.getAll1().subscribe(rs => {
        this.listTemplate = rs.data.templates;
        if (this.listTemplate && this.listTemplate.length > 0) {
          this.listTemplateTBThue = [];
          let listTemptTT78 = this.listTemplate.filter(p => p.is_decree_new || p.template_code == "CTT56");
          if (listTemptTT78 && listTemptTT78.length > 0)
            this.listTemplateTBThue = listTemptTT78;
          this.notifyService.getAll1().subscribe(result => {
            const data = result.data;
            const listRelease = result.data.invoice_releases;
            if (listRelease && listRelease.length > 0) {
              const listReleaseTBThue = listRelease.filter(p => p.release_status === 'DA_TB_THUE' || p.release_status === 'CHUA_TB_THUE' || p.release_status === 'DA_HUY_SO');
              if (listReleaseTBThue && listReleaseTBThue.length > 0) {
                listReleaseTBThue.forEach(release => {
                  this.listTemplate.forEach(template => {
                    if (template.id === release.template_id) {
                      const _obj = this.listTemplateTBThue.filter(p => p.id === template.id)[0];
                      if (!_obj) {
                        this.listTemplateTBThue.push(template);
                      }
                    }
                  });
                });
              }
            }
            if (this.listTemplateTBThue && this.listTemplateTBThue.length > 0) {
              this.listTemplateTBThue.forEach(item => {
                item.name_display = item.template_code + ' - ' + item.invoice_series;
              });
            }
          });
        }
      }, err => {
      }, () => {
        Library.hideloading();
      });
      /* Ly danh sch email ha n u vo */
      this.configsService.getAllEmails().subscribe(rs => {
        if (rs.status == "success") {
          this.listEmailConfigs = rs.data.config_mails;
        }
      }, err => {
      }, () => {
        Library.hideloading();
      });
      this.user = new User();
      this.user.is_use_all_template = this.currentUser.user.is_use_all_template;
      this.priority = this.user.is_use_all_template ? '1' : '0';
      this.disableTemptlate = this.user.is_use_all_template;

      this.user.permission_mails = this.currentUser.user.permission_mails;
      this.isReceiveAllEmail = VStringService._getValueToString(this.user.permission_mails) != "" ? '0' : '1';
      this.disableConfigEmail = VStringService._getValueToString(this.user.permission_mails) != "" ? false : true;

      this.user.order = this.totalCount + 1;
      this.disableRadioTemplates();
      this.disableRadioEmail();
    }
  }

  // x l chn quyn
  ketoanvien(e) {
    if (e.value === true) {
      this.roleGroups.KE_TOAN_VIEN = true;
      if (e.event && e.event.type === 'dxclick') {
        this.selectRoleGroup('KE_TOAN_VIEN');
      }
    } else {
      this.roleGroups.KE_TOAN_VIEN = false;
      this.unselectRoleGroup('KE_TOAN_VIEN');
    }
  }

  ketoantruong(e) {
    if (e.value === true) {
      this.roleGroups.KE_TOAN_TRUONG = true;
      if (e.event && e.event.type === 'dxclick') {
        this.selectRoleGroup('KE_TOAN_TRUONG');
      }
    } else {
      this.roleGroups.KE_TOAN_TRUONG = false;
      this.unselectRoleGroup('KE_TOAN_TRUONG');
    }
  }

  getSelectedItemsKeys(items) {
    let result = [];
    const that = this;
    items.forEach(function (item) {
      if (item.selected) {
        result.push(item.key);
      }
      if (item.items.length) {
        result = result.concat(that.getSelectedItemsKeys(item.items));
      }
    });
    return result;
  }


  pheduyet(e) {
    if (e.value === true) {
      this.roleGroups.PHE_DUYET = true;
      if (e.event && e.event.type === 'dxclick') {
        this.selectRoleGroup('PHE_DUYET');
      }
    } else {
      this.roleGroups.PHE_DUYET = false;
      this.unselectRoleGroup('PHE_DUYET');
    }
  }

  quantrihethong(e) {
    if (e.value === true) {
      this.roleGroups.QUAN_TRI_HE_THONG = true;
      if (e.event && e.event.type === 'dxclick') {
        this.selectRoleGroup('QUAN_TRI_HE_THONG');
      }
    } else {
      this.roleGroups.QUAN_TRI_HE_THONG = false;
      this.unselectRoleGroup('QUAN_TRI_HE_THONG');
    }
  }

  selectRoleGroup(roleName: string) {
    this.listpermissions.forEach(item => {
      if (item.roles.includes(roleName) && !this.treeList.instance.isRowSelected(item.id)) {
        if (!this.disablePermissions(item, "permissions"))
          this.treeList.instance.selectRows(item.id, false);
      }
    });
  }

  unselectRoleGroup(roleName: string) {
    const permissions = [];
    this.listpermissions.forEach(item => {
      if (!this.disablePermissions(item, "permissions") && item.roles.includes(roleName)) {
        permissions.push(item.id);
      }
    });
    this.treeList.instance.deselectRows(permissions);
  }
  getAllSelectedRowKeys() {
    if (this.treeList) {
      let _permissions = [];
      this.treeList.instance.getSelectedRowKeys('all').forEach(element => {
        let _obj = this.listpermissions.filter(p => p.id == element)[0];
        if (_obj) {
          _permissions.push(_obj);
        }
      });
      this.user.permissions = _permissions.map(p => p.code).join(',');
    }
    if (this.treeListTempt) {
      let _templates = [];
      this.treeListTempt.instance.getSelectedRowKeys('all').forEach(element => {
        let _obj = this.listTemplateTBThue.filter(p => p.id == element)[0];
        if (_obj) {
          _templates.push(_obj);
        }
      });
      this.user.template_ids = _templates.map(p => p.id).join(',');
    }
  }
  // x l submit
  onSubmit(e) {
    this.getAllSelectedRowKeys();


    if (!this.configs.system_parameter.display_delivery_information && !this.checkKTV && !this.checkKTT && !this.checkPD && !this.checkQTHT) {
      Library.notify(this._translate.TEXT.nhom_quyen_k_de_trong, 'error');
      return;
    }

    if (!this.user.is_use_all_template && VStringService._getValueToString(this.user.template_ids) === '') {
      Library.notify(this._translate.NOTIFY.you_must_delegate_at_least_one_invoice_template, 'error');
      return;
    }
    if (this.isReceiveAllEmail == "0" && VStringService._getValueToString(this.user.permission_mails) === '') {
      Library.notify(this._translate.NOTIFY.you_must_delegate_at_least_one_email_invoice_buy, 'error');
      return;
    }
    if (VStringService._getValueToString(this.user.permissions) === '') {
      Library.notify(this._translate.NOTIFY.you_must_delegate_at_least_one_permission, 'error');
      return;
    }
    if (this.remoteSigns && this.remoteSigns.is_use_remotesigning) {
      if (VStringService._getValueToString(this.remoteSigns.agreement_uuid) == '') {
        Library.notify(this._translate.CONFIGS.agreement_uuid + " " + this._translate.VALIDATION.required, 'error');
        return;
      }
      if (VStringService._getValueToString(this.remoteSigns.auth_mode) == '') {
        Library.notify(this._translate.CONFIGS.auth_mode + " " + this._translate.VALIDATION.required, 'error');
        return;
      }
      if (VStringService._getValueToString(this.remoteSigns.authorize_code) == '') {
        Library.notify(this._translate.CONFIGS.authorize_code + " " + this._translate.VALIDATION.required, 'error');
        return;
      }
    }
    Library.showloading();

    this.user.roles = '';

    if (this.configs.system_parameter.display_delivery_information) {
      if (this.valuePermission && this.valuePermission.length > 0) {
        this.valuePermission.forEach(item => {
          this.user.roles += item + ',';
        });
      }
      else
        this.user.roles = "";
    }
    else {
      for (const roleName in this.roleGroups) {
        if (this.roleGroups[roleName] === true) {
          this.user.roles += roleName + ',';
        }
      }
    }
    this.user.roles = this.user.roles.slice(0, -1);
    //console.log(this.selectedRowKeys);
    //console.log(this.user.roles);

    this.user.remote_signing_info = JSON.stringify(this.remoteSigns);
    if (!this.user.id) {
      if (this.isConnectEfyId) {
        this.user.username = this.user.email;

        let userClone = JSON.parse(JSON.stringify(this.user));
        userClone.status = this.user.status ? "HOAT_DONG" : "KHONG_HOAT_DONG";
        const data = { user: userClone };
        this.usersService.addEfyId(data).subscribe((response: any) => {
          if (response.status === 'success') {
            this.bsModalRef.content.successSaveItem = 'Success';
            Library.notify(this._translate.NOTIFY.add_success, 'success');
            this.bsModalRef.hide();
          } else {
            Library.notify(response.message, 'error');
          }
        }, err => { }, () => {
          Library.hideloading();
        });
      }
      else {
        let userClone = JSON.parse(JSON.stringify(this.user));
        userClone.status = this.user.status ? "HOAT_DONG" : "KHONG_HOAT_DONG";
        const data = {
          user: userClone
        };
        this.usersService.add(data).subscribe((response: any) => {
          if (response.status === 'success') {
            this.bsModalRef.content.successSaveItem = 'Success';
            Library.notify(this._translate.NOTIFY.add_success, 'success');
            this.bsModalRef.hide();
          } else {
            Library.notify(response.message, 'error');
          }
        }, err => { }, () => {
          Library.hideloading();
        });
      }
    } else {
      let dataEdit = null;
      let userClone = JSON.parse(JSON.stringify(this.user));
      userClone.status = this.user.status ? "HOAT_DONG" : "KHONG_HOAT_DONG";
      const id = this.user.id;
      if (this.user.hasOwnProperty('password')) {
        dataEdit = { user: userClone };
      } else {
        this.user.password = null;
        userClone.password = null;
        dataEdit = { user: userClone };
      }
      if (this.isConnectEfyId) {
        this.usersService.editEfyId(dataEdit, id).subscribe((response: any) => {
          if (response.status === 'success') {
            this.bsModalRef.content.successSaveItem = 'Success';
            Library.notify(this._translate.NOTIFY.update_success, 'success');
            const currentUser = this.authService.getCurrentUser();
            if (currentUser.user.id === id) {
              currentUser.user = dataEdit.user;
              this.authService.setCurentAuth(currentUser);
            }
            this.bsModalRef.hide();
          } else {
            Library.notify(response.message, 'error');
          }
        }, err => { }, () => {
          Library.hideloading();
        });
      }
      else {
        this.usersService.edit(dataEdit, id).subscribe((response: any) => {
          if (response.status === 'success') {
            this.bsModalRef.content.successSaveItem = 'Success';
            Library.notify(this._translate.NOTIFY.update_success, 'success');
            const currentUser = this.authService.getCurrentUser();
            if (currentUser.user.id === id) {
              currentUser.user = dataEdit.user;
              this.authService.setCurentAuth(currentUser);
            }
            this.bsModalRef.hide();
          } else {
            Library.notify(response.message, 'error');
          }
        }, err => { }, () => {
          Library.hideloading();
        });
      }
    }
    e.preventDefault();
  };

  selectTab(e) {
    const tab = e.itemData.code;
    this.getAllSelectedRowKeys();
    switch (tab) {
      case 'PHAN_QUYEN_HOAT_DONG':
        this.selectedRowKeys = [];
        this.textTabs = tab;
        this.isConfigAction = true;
        this.isConfigTemplate = false;
        this.isConfigSignatues = false;
        this.isConfigEmail = false;
        if (this.user.permissions) {
          const permissions = this.user.permissions.split(',');
          this.listpermissions.forEach(item => {
            if (permissions.includes(item.code)) {
              this.selectedRowKeys.push(item.id);
            }
          });
        }
        break;
      case 'PHAN_QUYEN_MAU_HOA_DON':
        this.textTabs = tab;
        this.isConfigTemplate = true;
        this.isConfigAction = false;
        this.isConfigSignatues = false;
        this.isConfigEmail = false;
        // if (this.user.template_ids && this.priority === '0') {
        //   const templates = this.user.template_ids.split(',');
        //   if (Array.isArray(this.listTemplateTBThue) && this.listTemplateTBThue.length > 0) {
        //     this.listTemplateTBThue.forEach(item => {
        //       if (templates.includes(item.id)) {
        //         this.selectedRowKeysTempt.push(item.id);
        //       }
        //     });
        //   }
        // }
        break;
      case 'CAU_HINH_CHU_KY_SO':
        this.textTabs = tab;
        this.isConfigSignatues = true;
        this.isConfigTemplate = false;
        this.isConfigAction = false;
        this.isConfigEmail = false;
        break;
      case 'EMAIL_NHAN_HOA_DON':
        this.textTabs = tab;
        this.isConfigEmail = true;
        this.isConfigTemplate = false;
        this.isConfigAction = false;
        this.isConfigSignatues = false;
        // if (VStringService._getValueToString(this.user.permission_mails)!=""  && this.isReceiveAllEmail === '0') {
        //   const emails = this.user.permission_mails.split(',');
        //   if (Array.isArray(this.listEmailConfigs) && this.listEmailConfigs.length > 0) {
        //     this.listEmailConfigs.forEach(item => {
        //       if (emails.includes(item.email)) {
        //         this.selectedRowKeysEmail.push(item.id);
        //       }
        //     });
        //   }
        // }
        break;
      default:
        break;
    }
  }
  onCellPrepared(e) {
    if (e.rowType == "data") {
      if (this.disablePermissions(e.row.data, "permissions")) {
        //e.cellElement.style.background = "#FF0000";
        // e.cellElement.style.background = "#F1F1F1";
        // e.cellElement.style.color = "#C2C2C2";
        e.cellElement.style.opacity = "0.7";
      }
    }
  }
  onEditorPreparing(e) {
    if (e.parentType == 'dataRow') {
      e.editorOptions.disabled = this.disablePermissions(e.row.data, "permissions");
    }
  }
  onCellPreparedTemplates(e) {
    if (e.rowType == "data") {
      if (this.disableTemplates(e.row.data)) {
        e.cellElement.style.opacity = "0.7";
      }
    }
  }
  onEditorPreparingTemplates(e) {
    //console.log(e.row.data);

    if (e.parentType == 'dataRow') {
      e.editorOptions.disabled = this.disableTemplates(e.row.data);
    }
  }

  disableGroupRoles() {
    if (this.isConfigAction) {
      let user = this.currentUser.user;

      if (VStringService._getValueToString(user.username).toLowerCase() == "superadmin"
        || user.is_super_admin == true) {
        return false;
      } else {
        if (this.id == user.id) {
          return true;
        } else return false;
      }
    } else {
      return true;
    }
  }


  disablePermissions(data, type) {
    let user = this.currentUser.user;
    let field = "code";
    if (VStringService._getValueToString(user.username).toLowerCase() == "superadmin"
      || user.is_super_admin == true) {
      return false;
    } else {
      if (this.id) {
        if (this.id == user.id) {/// sa chnh mnh th k cho sa quyn
          return true;
        } else {// Ging trng hp thm mi. user c quyn no th ch c sa quyn  ca user c sa
          if (user[type]) {
            let listPermissionsUserCurent = user[type].split(',');
            if (data && listPermissionsUserCurent.includes(data[field])) {
              return false;
            } else return true;
          } else
            return true;

        }
      } else {// thm mi user th user hin ti c quyn g th user thm mi ch c c quyn 
        if (user[type]) {
          let listPermissionsUserCurent = user[type].split(',');
          if (data && listPermissionsUserCurent.includes(data[field])) {
            return false;
          } else return true;
        } else
          return true;
      }
    }

  }

  disableRadioTemplates() {
    if (this.currentUser) {

      if (VStringService._getValueToString(this.currentUser.user.username).toLowerCase() == "superadmin"
        || this.currentUser.user.is_super_admin == true) {
        this.optionsPermissions = [
          { name: this._translate.TEXT.su_dung_tat_ca_mau_hd, code: '1', disabled: false },
          { name: this._translate.TEXT.theo_tung_mau_hd, code: '0', disabled: false }
        ];
      } else {
        if (this.currentUser.user.id == this.id) {
          this.optionsPermissions = [
            { name: this._translate.TEXT.su_dung_tat_ca_mau_hd, code: '1', disabled: true },
            { name: this._translate.TEXT.theo_tung_mau_hd, code: '0', disabled: true }
          ];
        } else {
          //Trng hp User A c phn quyn "c s dng tt c mu ha n" th khi sa quyn user khc s enable c 2 quyn "c s dng tt c mu ha n" v "Theo tng mu ha n"
          if (this.currentUser.user.is_use_all_template == true) {
            this.optionsPermissions = [
              { name: this._translate.TEXT.su_dung_tat_ca_mau_hd, code: '1', disabled: false },
              { name: this._translate.TEXT.theo_tung_mau_hd, code: '0', disabled: false }
            ];
          } else {//- Trng hp User A c phn quyn "Theo tng mu ha n":
            //+ Nu User B ang c phn quyn "c s dng tt c mu ha n" th disable c 2 quyn ny
            if (this.user.is_use_all_template) {
              this.optionsPermissions = [
                { name: this._translate.TEXT.su_dung_tat_ca_mau_hd, code: '1', disabled: true },
                { name: this._translate.TEXT.theo_tung_mau_hd, code: '0', disabled: true }
              ];
            } else //+ Nu User B c phn quyn "Theo tng mu ha n" th enable quyn "Theo tng mu ha n", danh sch mu ha n bao gm cc mu user A c phn quyn
              this.optionsPermissions = [
                { name: this._translate.TEXT.su_dung_tat_ca_mau_hd, code: '1', disabled: true },
                { name: this._translate.TEXT.theo_tung_mau_hd, code: '0', disabled: false }
              ];
          }
        }
      }


    }
  }

  disableTemplates(data) {
    let user = this.currentUser.user;
    if (VStringService._getValueToString(user.username).toLowerCase() == "superadmin"
      || user.is_super_admin == true || user.is_use_all_template == true) {
      return false;
    } else {
      if (this.id) {
        if (this.id == user.id) {/// sa chnh mnh th k cho sa quyn
          return true;
        } else {// Ging trng hp thm mi. user c quyn no th ch c sa quyn  ca user c sa
          if (user.template_ids) {
            let listTemplatesUserCurent = user.template_ids.split(',');
            if (data && listTemplatesUserCurent.includes(data.id)) {
              return false;
            } else return true;
          } else
            return true;

        }
      } else {// thm mi user th user hin ti c quyn g th user thm mi ch c c quyn 
        if (user.template_ids) {
          let listTemplatesUserCurent = user.template_ids.split(',');
          if (data && listTemplatesUserCurent.includes(data.id)) {
            return false;
          } else return true;
        } else
          return true;
      }
    }

  }

  disableRadioEmail() {
    if (this.currentUser) {
      if (VStringService._getValueToString(this.currentUser.user.username).toLowerCase() == "superadmin"
        || this.currentUser.user.is_super_admin == true) {
        this.optionsEmails = [
          { name: this._translate.TEXT.nhan_thu_tu_tat_ca_email, code: '1', disabled: false },
          { name: this._translate.TEXT.nhan_thu_tu_email, code: '0', disabled: false }
        ];
      } else {
        if (this.currentUser.user.id == this.id) {
          this.optionsEmails = [
            { name: this._translate.TEXT.nhan_thu_tu_tat_ca_email, code: '1', disabled: true },
            { name: this._translate.TEXT.nhan_thu_tu_email, code: '0', disabled: true }
          ];
        } else {
          //Trng hp User A c phn quyn "Nhn th t tt c cc email" th khi sa quyn user khc s enable c 2 quyn "Nhn th t tt c cc email" v "Nhn th t email"
          if (VStringService._getValueToString(this.currentUser.user.permission_mails) == "") {
            this.optionsEmails = [
              { name: this._translate.TEXT.nhan_thu_tu_tat_ca_email, code: '1', disabled: false },
              { name: this._translate.TEXT.nhan_thu_tu_email, code: '0', disabled: false }
            ];
          } else {//- Trng hp User A c phn quyn "Nhn th t email":
            //+ Nu User B ang c phn quyn "Nhn th t tt c cc email" th disable c 2 quyn ny
            if (VStringService._getValueToString(this.user.permission_mails) == "") {
              this.optionsEmails = [
                { name: this._translate.TEXT.nhan_thu_tu_tat_ca_email, code: '1', disabled: true },
                { name: this._translate.TEXT.nhan_thu_tu_email, code: '0', disabled: true }
              ];
            } else //+ Nu User B c phn quyn "Nhn th t email" th enable quyn "Nhn th t email", danh sch mu ha n bao gm cc mu user A c phn quyn
              this.optionsEmails = [
                { name: this._translate.TEXT.nhan_thu_tu_tat_ca_email, code: '1', disabled: true },
                { name: this._translate.TEXT.nhan_thu_tu_email, code: '0', disabled: false }
              ];
          }
        }

      }

    }
  }

  disableEmailItem(data) {
    let user = this.currentUser.user;
    if (VStringService._getValueToString(user.username).toLowerCase() == "superadmin"
      || user.is_super_admin == true || VStringService._getValueToString(user.permission_mails) == "") {
      return false;
    } else {
      if (this.id) {
        if (this.id == user.id) {/// sa chnh mnh th k cho sa quyn
          return true;
        } else {// Ging trng hp thm mi. user c quyn no th ch c sa quyn  ca user c sa
          if (user.permission_mails) {
            let listTemplatesUserCurent = user.permission_mails.split(',');
            if (data && listTemplatesUserCurent.includes(data.email)) {
              return false;
            } else return true;
          } else
            return true;

        }
      } else {// thm mi user th user hin ti c quyn g th user thm mi ch c c quyn 
        if (user.permission_mails) {
          let listTemplatesUserCurent = user.permission_mails.split(',');
          if (data && listTemplatesUserCurent.includes(data.email)) {
            return false;
          } else return true;
        } else
          return true;
      }
    }

  }
  onCellPreparedEmail(e) {
    if (e.rowType == "data") {
      if (this.disableEmailItem(e.row.data)) {
        //e.cellElement.style.background = "#FF0000";
        // e.cellElement.style.background = "#F1F1F1";
        // e.cellElement.style.color = "#C2C2C2";
        e.cellElement.style.opacity = "0.7";
      }
    }
  }
  onEditorPreparingEmail(e) {
    if (e.parentType == 'dataRow') {
      e.editorOptions.disabled = this.disableEmailItem(e.row.data);
    }
  }

  onItemSelectionChanged(e) {
    if (this.textTabs === 'PHAN_QUYEN_HOAT_DONG') {

    }
  }

  onItemSelectionChangedTempt(e) {
    if (this.textTabs === 'PHAN_QUYEN_MAU_HOA_DON') {
      this.user.template_ids = '';
      const selectedKeyTemplate = this.treeListTempt.instance.getSelectedRowKeys('all');
      for (const idtempt of selectedKeyTemplate) {
        this.listTemplateTBThue.forEach(item => {
          if (item.id === idtempt) {
            this.user.template_ids += item.id + ',';
          }
        });
      }
      this.user.template_ids = this.user.template_ids.slice(0, -1);
    }
  }

  onValueChanged(e) {
    if (e.value === '1') {
      this.disableTemptlate = true;
      this.user.is_use_all_template = true;
    } else {
      this.disableTemptlate = false;
      this.user.is_use_all_template = false;
    }
  }

  getChecklistBranch($event) {
    if ($event.length > 0) {
      if ($event.indexOf('TAT_CA') !== -1) {
        const value = $event.filter(item => {
          return item === 'TAT_CA';
        });
        this.user.role_branch = value.toString();
      } else {
        this.user.role_branch = $event.toString().replace(/,/g, ';');
      }
    } else {
      this.user.role_branch = null;
    }
  }

  onValueESChanged = (e) => {
    if (this.isConnectEfyId)
      this.checkUserEfyId(e.value);
  }
  checkUserEfyId(username) {
    Library.showloading();
    this.registerService.checkUserUseEfyId(username).subscribe(
      (res: any) => {
        if (res.status === 'success') {
          if (res.data.user) {
            this.user.name = res.data.user.name;
            this.user.mobile = res.data.user.mobile;
            this.user.email = res.data.user.email;
          }
        }
        else {
          this.user.name = "";
          this.user.mobile = "";
          // Library.notify(res.message, 'error');
        }

      }, error => Library.notify(error, 'error'),
      () => Library.hideloading()
    );
  }
  onPermissionSelectChanged(e) {
    const nodes = e.component.getNodes();
    let uncheckPermission = nodes.filter(p => !p.selected);
    let checkPermission = nodes.filter(p => p.selected);
    uncheckPermission.forEach(item => {
      this.unselectRoleGroup(item.key);
    });
    checkPermission.forEach(item => {
      this.selectRoleGroup(item.key);
    });
    this.valuePermission = this.getSelectedItemsKeys(nodes);

  }
  /* Check remoteSigning */
  onIsRemoteSigning = (e) => {
    if (e.value) {
      this.isDisRemoteSigning = false;
    } else {
      this.isDisRemoteSigning = true;
    }
  }
  onEmailValueChanged(e) {
    if (e.value === '1') {
      this.disableConfigEmail = true;
      this.selectedRowKeysEmail = [];
      this.user.permission_mails = '';
    } else {
      this.disableConfigEmail = false;
    }
  }
  onItemSelectionChangedEmail(e) {
    if (this.textTabs === 'EMAIL_NHAN_HOA_DON') {
      this.user.permission_mails = '';
      const selectedKeyEmail = this.treeListEmail.instance.getSelectedRowKeys('all');
      for (const idtempt of selectedKeyEmail) {
        this.listEmailConfigs.forEach(item => {
          if (item.id === idtempt) {
            this.user.permission_mails += item.email + ',';
          }
        });
      }
      this.user.permission_mails = this.user.permission_mails.slice(0, -1);
    }
  }
}
