import { Component, OnInit, ViewChild } from '@angular/core';

import { User, UsersService } from '@modules/users/shared';
import { BsModalRef } from 'ngx-bootstrap/modal/bs-modal-ref.service';
import { BsModalService } from 'ngx-bootstrap/modal';
import { AddComponent } from '../add/add.component';
import { Library, AuthService, VStringService } from '@core/index';
import { DxDataGridComponent } from 'devextreme-angular';
import { TranslateService } from '@ngx-translate/core';
import { ListsService } from '@modules/lists/shared';
import { NotifyEfyComponent } from '@shared/index';
import { VSelectBoxService } from '@core/library/public/v-select-box.service';
import { PermissionComponent } from '../permission/permission.component';
@Component({
  selector: 'app-index',
  templateUrl: './index.component.html',
  styleUrls: ['./index.component.scss']
})
export class IndexComponent implements OnInit {
  @ViewChild(DxDataGridComponent) dataGrid: DxDataGridComponent;
  dataSource: any = {};
  dataSourcePermission: any = {};
  selectedRows = [];
  selectedRowsPermission = [];
  noDataText = 'Cha c ngi dng no';
  bsModalRef: BsModalRef;
  statusFilter: any;
  _translate: any;
  role_branch: boolean;
  isConvertEfyId: boolean = false;
  //// Bin  n hin nt chuyn i efy-id
  isUserConverted: boolean = false;
  idLists = 'AEFB016C-23BF-4A5D-AEF9-28C72427551B';
  listRoleBranchOrigin: Array<any>;
  bsModalSignRef: BsModalRef;
  currentUser: any;
  textFullName: any;
  tabs = [];
  isUser = true;
  isPermissions = false;
  listtypes: any;
  listtypeId: any;
  configs: any;
  permissionByListType: any;
  isOpenConvertEfyId: boolean = false;
  constructor(
    private authService: AuthService,
    private usersService: UsersService,
    private modalService: BsModalService,
    public translate: TranslateService,
    private listService: ListsService,
  ) {
    this._translate = this.authService.getTranslate();
    translate.use(this.authService.getLanguage());
    this.statusFilter = [{
      text: this._translate.GRID.GENERAL.active,
      value: 'HOAT_DONG'
    }, {
      text: this._translate.GRID.GENERAL.inactive,
      value: 'KHONG_HOAT_DONG'
    }];
    this.tabs = VSelectBoxService.tabUser(this._translate);
  }


  ngOnInit() {
    this.configs = this.authService.getConfigs();
    this.loadList();
    this.getListtypes();
    this.role_branch = this.listService.getDataConfigLocalStorage();
    this.currentUser = this.authService.getCurrentUser();
    this.textFullName = this._translate.MENU.user;
    if (this.currentUser && this.currentUser.account) {
      if (this.currentUser.account.connect_efy_id) {
        this.isConvertEfyId = true;
        this.textFullName = this._translate.CONTROLL.LABEL.fullname;
      }
      else
        this.isOpenConvertEfyId = true;
    }
    // this.listService.getDM().subscribe(value => console.log(value)) ;
  }

  loadList() {
    this.dataSource.store = this.usersService.getAll();
  }
  loadListPermission() {
    let params = `filter_groups[0][filters][0][key]=listtype_id&filter_groups[0][filters][0][value]=${this.listtypeId}&filter_groups[0][filters][0][operator]=eq`;
    this.dataSourcePermission.store = this.listService.getAll(params);
  }
  async getListRole() {
    let arrList: any;
    arrList = await this.usersService.getRoles().then(value => {
      if (value.status === 'success') {
        return value.data.DM_NHOM_QUYEN;
      }
      else
        return [];
    });
    return arrList;
  }
  async add() {
    /* Ngi s dng */
    if (this.isUser) {
      this.listRoleBranchOrigin = [];
      if (this.role_branch) {
        this.listRoleBranchOrigin = await this.listService.handleListRoleBranchUser();
      }
      this.permissionByListType = await this.getListRole();
      const initialState = {
        totalCount: this.dataGrid.instance.totalCount(),
        role_branch: this.role_branch,
        roleBranchStatus: this.listRoleBranchOrigin,
        permissionByListType: this.permissionByListType
      };
      this.bsModalRef = this.modalService.show(AddComponent, {
        class: 'modal-lg full-modal', initialState, backdrop: 'static',
        keyboard: false
      });
      this.modalService.onHide.subscribe(() => {
        if (this.bsModalRef.content.successSaveItem) {
          // this.dataGrid.instance.refresh();
          this.dataSource = {};
          this.loadList();
        }
      });
    }
    /* Phn quyn */
    else {
      const initialState = {
        totalCount: this.dataGrid.instance.totalCount(),
        listtypeId: this.listtypeId,
        listtypes: this.listtypes
      };
      this.bsModalRef = this.modalService.show(PermissionComponent, {
        class: 'modal-lg full-modal', initialState, backdrop: 'static',
        keyboard: false
      });
      this.modalService.onHide.subscribe(() => {
        if (this.bsModalRef.content.successSaveItem) {
          //this.dataGrid.instance.refresh();
          this.dataSourcePermission = {};
          this.loadListPermission();
        }
      });
    }
  }
  getListtypes() {
    this.listService.getAllListtype().subscribe(res => {
      this.listtypes = res.data.listtypes;
      if (this.listtypes) {
        let types = this.listtypes.filter(p => p.code == "DM_NHOM_QUYEN")[0];
        if (types) {
          this.listtypeId = types.id;
          this.loadListPermission();
        }
      }
    });
  }
  async edit(data) {
    /* Ngi s dng */
    if (this.isUser) {
      const id = data ? data.id : (this.selectedRows[0].length === 0 ? '' : this.selectedRows[0]);
      if (!id) {
        Library.notify(this._translate.NOTIFY.select_object, 'error');
        return;
      } else {
        if (this.role_branch) {
          this.listRoleBranchOrigin = await this.listService.handleListRoleBranchUser();
        }
        this.permissionByListType = await this.getListRole();
        const initialState = {
          id: id,
          role_branch: this.role_branch,
          roleBranchStatus: this.listRoleBranchOrigin,
          permissionByListType: this.permissionByListType
        };
        this.bsModalRef = this.modalService.show(AddComponent, {
          class: 'modal-lg full-modal', initialState, backdrop: 'static',
          keyboard: false
        });
        this.modalService.onHide.subscribe(() => {
          if (this.bsModalRef.content.successSaveItem) {
            this.dataSource = {};
            this.loadList();
          }
        });
      }
    }
    /* Phn quyn */
    else {
      const id = data ? data.id : (this.selectedRowsPermission[0].length === 0 ? '' : this.selectedRowsPermission[0]);
      if (!id) {
        Library.notify(this._translate.NOTIFY.select_object, 'error');
        return;
      } else {
        const initialState = {
          id: id,
          listtypeId: this.listtypeId,
          listtypes: this.listtypes
        };
        this.bsModalRef = this.modalService.show(PermissionComponent, {
          class: 'modal-lg full-modal', initialState, backdrop: 'static',
          keyboard: false
        });
        this.modalService.onHide.subscribe(() => {
          if (this.bsModalRef.content.successSaveItem) {
            this.dataSourcePermission = {};
            this.loadListPermission();
          }
        });
      }
    }
  }

  delete(data) {
    /* Ngi s dng */
    if (this.isUser) {
      let id = data ? data.id : (this.selectedRows[0].length == 0 ? '' : this.selectedRows[0]);
      if (!id) {
        Library.notify(this._translate.NOTIFY.select_object, 'error');
        return;
      } else if (!data && this.selectedRows.length > 1) {
        Library.notify(this._translate.NOTIFY.select_only_one, 'error');
      } else {
        var result = Library.confirm(this._translate.CONFIRM.delete, this._translate.NOTIFY.notify);
        if (result) {
          result.then((dialogResult) => {
            if (dialogResult) {
              this.usersService.delete(id).subscribe((response: any) => {
                if (response.status === 'success') {
                  Library.notify(this._translate.NOTIFY.delete_success, 'success');
                  this.dataSource = {};
                  this.loadList();
                } else {
                  Library.notify(response.message, 'error');
                }
              });
            }
          });
        }
      }
    }
    /* Phn quyn */
    else {
      let id = data ? data.id : (this.selectedRowsPermission[0].length == 0 ? '' : this.selectedRowsPermission[0]);
      if (!id) {
        Library.notify(this._translate.NOTIFY.select_object, 'error');
        return;
      } else if (!data && this.selectedRows.length > 1) {
        Library.notify(this._translate.NOTIFY.select_only_one, 'error');
        return;
      } else {
        if (data.code == "KE_TOAN_VIEN" || data.code == "KE_TOAN_TRUONG" || data.code == "PHE_DUYET" || data.code == "QUAN_TRI_HE_THONG") {
          Library.notify(this._translate.NOTIFY.you_may_not_remove_this_permission, 'error');
          return;
        }
        var result = Library.confirm(this._translate.CONFIRM.delete, this._translate.NOTIFY.notify);
        if (result) {
          result.then((dialogResult) => {
            if (dialogResult) {
              this.listService.delete(id).subscribe((response: any) => {
                if (response.status === "success") {
                  Library.notify(this._translate.NOTIFY.delete_success, 'success');
                  this.dataSourcePermission = {};
                  this.loadListPermission();
                } else {
                  Library.notify(response.message, 'error');
                }
              });
            }
          });
        }
      }
    }
  }

  setRole(data) {
    let rolesDisplay = '';
    let roles;
    if (data.roles) {
      roles = data.roles.split(',');
      rolesDisplay += roles.includes('KE_TOAN_VIEN') ? 'K ton vin, ' : '';
      rolesDisplay += roles.includes('KE_TOAN_TRUONG') ? 'K ton trng, ' : '';
      rolesDisplay += roles.includes('PHE_DUYET') ? 'Ph duyt, ' : '';
      rolesDisplay += roles.includes('QUAN_TRI_HE_THONG') ? 'Qun tr h thng, ' : '';
    }
    rolesDisplay = rolesDisplay.slice(0, -2);
    return rolesDisplay;
  }
  convertEfyId() {
    this.bsModalSignRef = this.modalService.show(NotifyEfyComponent, {
      backdrop: 'static',
      keyboard: false,
      class: 'notify-modal',
    });
    const subModal = this.modalService.onHide.subscribe(() => {
      if (this.bsModalSignRef.content.successSaveItem) {
        subModal.unsubscribe();
      }
    });
  }
  onEditorPreparing(e) {
    if (e.dataField === 'name' && e.parentType == "dataRow") {
      e.editorName = 'dxTextArea';
    }
  }
  selectTab(e) {
    const tab = e.itemData.code;
    switch (tab) {
      case 'NGUOISUDUNG':
        this.isUser = true;
        this.isPermissions = false;
        break;
      case 'NHOMQUYEN':
        this.isPermissions = true;
        this.isUser = false;
        break;
      default:
        break;
    }
  }
}
