import { Component, OnInit, ViewChild, ChangeDetectorRef } from '@angular/core';
import { BsModalService } from 'ngx-bootstrap/modal';
import { BsModalRef } from 'ngx-bootstrap/modal/bs-modal-ref.service';
import { User, UsersService } from '@modules/users/shared';
import { DxFormComponent, DxTreeListComponent } from 'devextreme-angular';
import { Library, AuthService, VSelectBoxService, VStringService } from '@core/index';
import { TranslateService } from '@ngx-translate/core';
import { TemplateService } from '@modules/template/shared';
import { NotifyService } from '@modules/release-notify/shared';
import { DropdownCheckboxComponent } from '@shared/components/dropdown-checkbox/dropdown-checkbox.component';
import { RegisterService } from '@modules/auth/shared';
import { List, ListsService } from '@modules/lists/shared';
@Component({
  selector: 'app-permission',
  templateUrl: './permission.component.html',
  styleUrls: ['./permission.component.scss']
})
export class PermissionComponent implements OnInit {
  @ViewChild(DxFormComponent) form: DxFormComponent;
  // @ViewChild(DxTreeListComponent) treeList: DxTreeListComponent;
  @ViewChild('listpermis') treeList: DxTreeListComponent;
  @ViewChild('listTempt') treeListTempt: DxTreeListComponent;
  @ViewChild('dropdownCheckboxComponent') dropdownCheckboxComponent: DropdownCheckboxComponent;

  // form validate
  user: any;
  id: any;
  password = '';
  changePermission: boolean;
  passwordOptions: any = {
    mode: 'password',
    value: this.password
  };
  namePattern: any = /^[^0-9]+$/;
  passwordPattern: any = /^.{6,}$/;
  emailPattern: any = /^([a-zA-Z0-9_\-\.]+)@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.)|(([a-zA-Z0-9\-]+\.)+))([a-zA-Z]{2,4}|[0-9]{1,3})(\]?)$/;
  userNamePattern: any = /^[a-zA-Z0-9_]{6,50}$/;
  phonePattern: any = /^(\(?\+?[0-9]*\)?)?[0-9_\- \(\)]*$/;
  isValidate = true;
  order: number;
  totalCount: any;
  disUserName = false;
  optionsPermissions: any;
  // treelist
  selectedRowKeys: any[] = [];
  selectedRowKeysTempt: any[] = [];
  recursiveSelectionEnabled = true;
  recursiveSelectionEnabledTempt = true;
  roles: string;
  permissions: string;
  successSaveItem: any;
  disableTemptlate = true;
  // quyn
  listpermissions: any;
  roleGroups = {
    KE_TOAN_VIEN: false,
    KE_TOAN_TRUONG: false,
    PHE_DUYET: false,
    QUAN_TRI_HE_THONG: false
  };
  checkKTV = false;
  checkKTT = false;
  checkPD = false;
  checkQTHT = false;
  _translate: any;
  isConfigAction = true;
  isConfigTemplate = false;
  listTemplate: any;
  listTemplateTBThue: any[] = [];
  tabs = [];
  priority: any;
  role_branch = false;
  roleBranchStatus: Array<any>;
  textTabs: string;
  lbl: any = {};
  isConnectEfyId = false;
  isUserConverted = false;
  currentUser: any;
  disableEmail = false;
  list: any;
  listtypes: any;
  listtypeId: any;
  json_data = {};
  passwordComparison = () => {
    return this.form.instance.option('formData').password;
  };

  constructor(
    private authService: AuthService,
    public bsModalRef: BsModalRef,
    private usersService: UsersService,
    private modalService: BsModalService,
    private ref: ChangeDetectorRef,
    public translate: TranslateService,
    private templateService: TemplateService,
    private notifyService: NotifyService,
    private registerService: RegisterService,
    private listsService: ListsService,
  ) {
    this._translate = this.authService.getTranslate();
    translate.use(this.authService.getLanguage());
    this.tabs = VSelectBoxService._tabPermissionUser(this._translate);
    this.textTabs = this.tabs[0].code;
    this.optionsPermissions = [
      { name: 'c s dng tt c mu ha n', code: '1' },
      { name: 'Theo tng mu ha n', code: '0' }
    ];
  }

  ngOnInit() {
    // this.getPermissions();
    this.currentUser = this.authService.getCurrentUser();
    if (this.currentUser && this.currentUser.account) {
      if (this.currentUser.account.connect_efy_id == "1")
        this.isConnectEfyId = true;
    }
    this.getList();
    if (this.id) {
      this.isValidate = false;
      this.disUserName = true;
    }
    this.lbl = {
      label1: this._translate.CONTROLL.LABEL.user_will_use_efy_id_login + ' ' + this._translate.CONTROLL.LABEL.learn_more_about_efy_id,
      label2: this._translate.CONTROLL.LABEL.system_auto_email,
    };
  }

  getList() {
    if (this.id) { // nu l edit
      Library.showloading();
     // this.changePermission = true;
      this.usersService.getPermissions().subscribe(response => {
        this.listpermissions = response.data.permissions;
        this.listsService.getList(this.id, "1").subscribe(response => {
          this.list = response.data.list;
          if (this.list.status && this.list.status === "HOAT_DONG") {
            this.list.status = true;
          } else if (this.list.status && this.list.status === "KHONG_HOAT_DONG") {
            this.list.status = false;
          }
          if (this.list.json_data) {
           this.changePermission = this.list.json_data.change_permission;
            // for (const key in this.list.json_data) {
            //   if (this.list.json_data.hasOwnProperty(key)) {
            //     const value = this.list.json_data[key];
            //     this.list[key] = value;
            //   }
            // }
          }
          if (this.list.note) {
            const permissions = this.list.note.split(',');
            this.listpermissions.forEach(item => {
              if (permissions.includes(item.code) && !this.treeList.instance.isRowSelected(item.id)) {
                this.selectedRowKeys.push(item.id);
              }
            });
          }
          Library.hideloading();
        });
      });
    } else {
      this.usersService.getPermissions().subscribe(response => {
        this.listpermissions = response.data.permissions;
        this.list = new List();
        this.list.order = this.totalCount + 1;
      });
    }
  }
  onCellPrepared(e) {
    console.log(e);
    
    if (e.rowType == "data") {
      
    }
  }
  // x l chn quyn
  ketoanvien(e) {
    if (e.value === true) {
      this.roleGroups.KE_TOAN_VIEN = true;
      if (e.event && e.event.type === 'dxclick') {
        this.selectRoleGroup('KE_TOAN_VIEN');
      }
    } else {
      this.roleGroups.KE_TOAN_VIEN = false;
      this.unselectRoleGroup('KE_TOAN_VIEN');
    }
  }

  ketoantruong(e) {
    if (e.value === true) {
      this.roleGroups.KE_TOAN_TRUONG = true;
      if (e.event && e.event.type === 'dxclick') {
        this.selectRoleGroup('KE_TOAN_TRUONG');
      }
    } else {
      this.roleGroups.KE_TOAN_TRUONG = false;
      this.unselectRoleGroup('KE_TOAN_TRUONG');
    }
  }

  getSelectedItemsKeys(items) {
    let result = [];
    const that = this;
    items.forEach(function (item) {
      if (item.selected) {
        result.push(item.key);
      }
      if (item.items.length) {
        result = result.concat(that.getSelectedItemsKeys(item.items));
      }
    });
    return result;
  }


  pheduyet(e) {
    if (e.value === true) {
      this.roleGroups.PHE_DUYET = true;
      if (e.event && e.event.type === 'dxclick') {
        this.selectRoleGroup('PHE_DUYET');
      }
    } else {
      this.roleGroups.PHE_DUYET = false;
      this.unselectRoleGroup('PHE_DUYET');
    }
  }

  quantrihethong(e) {
    if (e.value === true) {
      this.roleGroups.QUAN_TRI_HE_THONG = true;
      if (e.event && e.event.type === 'dxclick') {
        this.selectRoleGroup('QUAN_TRI_HE_THONG');
      }
    } else {
      this.roleGroups.QUAN_TRI_HE_THONG = false;
      this.unselectRoleGroup('QUAN_TRI_HE_THONG');
    }
  }

  selectRoleGroup(roleName: string) {
    this.listpermissions.forEach(item => {
      if (item.roles.includes(roleName) && !this.treeList.instance.isRowSelected(item.id)) {
        this.treeList.instance.selectRows(item.id, false);
      }
    });
  }

  unselectRoleGroup(roleName: string) {
    const permissions = [];
    this.listpermissions.forEach(item => {
      if (item.roles.includes(roleName)) {
        permissions.push(item.id);
      }
    });
    this.treeList.instance.deselectRows(permissions);
  }
  // x l submit
  onSubmit(e) {
    Library.showloading();
    if (this.list.status) {
      this.list.status = "HOAT_DONG";
    } else {
      this.list.status = "KHONG_HOAT_DONG";
    }
    this.list.listtype_id = this.listtypeId;
    this.list.note = this.permissions;
    if(VStringService._getValueToString(this.list.note)=="")
    {
      Library.notify(this._translate.NOTIFY.you_choose_at_least_one, 'error');
      return;
    }
    this.listtypes.forEach(listtype => {
      if (this.listtypeId == listtype.id && listtype.json_data) {
        listtype.json_data.forEach(item => {
          for (const key in this.list) {
            if (item.code == key) {
              this.json_data[key] = this.list[key];
            }
          }
        });
      }
    });

    this.list.json_data = this.json_data;
    this.list.json_data.change_permission = this.changePermission;
    //let data = {list: this.list};
    let data = JSON.parse(JSON.stringify({ list: this.list }));
    for (const key in data.list.json_data) {
      if (data.list.json_data[key] == data.list[key]) {
        delete data.list[key];
      }
    }
    if (!this.list.id) {
      this.listsService.add(data).subscribe((response: any) => {
        if (response.status == "success") {
          this.bsModalRef.content.successSaveItem = "success";
          Library.notify(this._translate.NOTIFY.add_success, "success");
            this.bsModalRef.content.successSaveData = this.list;
            this.bsModalRef.hide();
        } else {
          Library.notify(response.message, "error");
        }
      }, err => { }, () => {
        Library.hideloading();
      });
    }
    else {
      let id = this.list.id;
      this.listsService.edit(data, id).subscribe((response: any) => {
        if (response.status == "success") {
          this.bsModalRef.content.successSaveItem = "success";
          Library.notify(this._translate.NOTIFY.update_success, "success");
          this.bsModalRef.hide();
        } else {
          Library.notify(response.message, "error");
        }
      }, err => { }, () => {
        Library.hideloading();
      });
    }
  }

  onItemSelectionChanged(e) {
      this.permissions = '';
      const selectedKey = this.treeList.instance.getSelectedRowKeys('all');
      for (const role of selectedKey) {
        this.listpermissions.forEach(item => {
          if (item.id === role && item.permission === true) {
            this.permissions += item.code + ',';
          }
        });
      }
      this.permissions = this.permissions.slice(0, -1);
  }

  onValueChanged(e) {
    if (e.value === '1') {
      this.disableTemptlate = true;
      this.user.is_use_all_template = true;
    } else {
      this.disableTemptlate = false;
      this.user.is_use_all_template = false;
    }
  }

  getChecklistBranch($event) {
    if ($event.length > 0) {
      if ($event.indexOf('TAT_CA') !== -1) {
        const value = $event.filter(item => {
          return item === 'TAT_CA';
        });
        this.user.role_branch = value.toString();
      } else {
        this.user.role_branch = $event.toString().replace(/,/g, ';');
      }
    } else {
      this.user.role_branch = null;
    }
  }

  onValueESChanged = (e) => {
    if (this.isConnectEfyId)
      this.checkUserEfyId(e.value);
  }
  checkUserEfyId(username) {
    Library.showloading();
    this.registerService.checkUserUseEfyId(username).subscribe(
      (res: any) => {
        if (res.status === 'success') {
          if (res.data.user) {
            this.user.name = res.data.user.name;
            this.user.mobile = res.data.user.mobile;
            this.user.email = res.data.user.email;
          }
        }
        else {
          this.user.name = "";
          this.user.mobile = "";
         // Library.notify(res.message, 'error');
        }
      }, error => Library.notify(error, 'error'),
      () => Library.hideloading()
    );
  }
  checkboxChangePermission($event)
  {
    if ($event.target.value === 'changePermission' && $event.target.checked === true) {
      this.changePermission = $event.target.checked;
    } else {
      this.changePermission = false;
    }
  }
}
