import { Injectable } from '@angular/core';
import { VStringService } from '@core/index';

@Injectable({
  providedIn: 'root'
})
export class NumberUtilsService {

  constructor() {
  }

  static _attemptConvertBool(value) {
    if (value) {
      switch (value.toLowerCase()) {
        case true:
        case 'true':
        case 1:
        case '1':
        case 'x':
        case 'hoat_dong':
          return true;
        default:
          return false;
      }
    } else {
      return false;
    }
  }

  static _attemptConvertFloat(str) {
    // let tem = str.replace(",", "");
    if (str) {// && str.trim()) {
      const tem = str.replace(new RegExp(',', 'g'), '');
      return parseFloat(tem);
    } else {
      return null;
    }
  }
}
